import Vue from 'vue'
import VueRouter from 'vue-router'
import wysiwygMathJax from "@/components/wysiwygMathJax";
import wysiwygMathQuill from "@/components/wysiwygMathQuill";

Vue.use(VueRouter)

const routes = [
  {
    path: '/jax',
    name: 'wysiwygMathJax',
    component: wysiwygMathJax
  },
  {
    path: '/quill ',
    name: 'wysiwygMathQuill',
    component: wysiwygMathQuill
  }
]

const router = new VueRouter({
  routes
})

export default router
