                <h5 class="modal-title">{{ isInEditMode ? 'Редактировать формулу' : 'Вставить формулу' }}</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="showModal = false">
                  <span aria-hidden="true">&times;</span>
