              </div>
              <div class="modal-body">
                <div class="d-flex mb-3">
                  <div class="d-flex">
                    <div style="position:relative;"
                    >
                      <div class="py-1 px-2 text-center"
                           style="background-color: #ffffff; position:relative; cursor: pointer; user-select: none;
                           margin: 1px; border: rgb(133, 133, 133) solid 1px; border-radius: 4px"
                           @click="showLetters = !showLetters"
                           :style="`${showLetters ? 'border: black solid 2px; margin: 0;' : ''}`"
                      >
                        \(\alpha \beta \delta\)
                      </div>
                      <div v-show="showLetters"
                           class=""
                           style="z-index: 100; background-color: #ffffff; position: absolute; top: 100%; left: 0;
                           margin-top: 8px; border: black 2px solid; border-radius: 4px; padding: 4px;
                           display: grid; grid-template-columns: repeat(8, 1fr); flex-wrap: wrap; text-align: center"
                      >
                        <div v-for="(letter, index) in letters"
                             :key="index"
                             @click="insertLetter(letter)"
                             class="letter"
                        >
                          \({{ letter }}\)
                        </div>
                      </div>
                    </div>

                    <div style="position:relative;"
                         class="ml-2"
                    >
                      <div class="py-1 px-2 text-center"
                           style="background-color: #ffffff; position:relative; cursor: pointer; user-select: none;
                           margin: 1px; border: rgb(133, 133, 133) solid 1px; border-radius: 4px"
                           @click="showOperators = !showOperators"
                           :style="`${showOperators ? 'border: black solid 2px; margin: 0;' : ''}`"
                      >
                        \(\sum\)
                      </div>
                      <div v-show="showOperators"
                           class=""
                           style="z-index: 100; background-color: #ffffff; position: absolute; top: 100%; left: 0;
                           margin-top: 8px; border: black 2px solid; border-radius: 4px; padding: 4px;
                           display: grid; grid-template-columns: repeat(4, 1fr); flex-wrap: wrap; text-align: center"
                      >
                        <div v-for="(operator, index) in operators"
                             :key="index"
                             @click="insertOperator(operator)"
                             class="letter"
                        >
                          \({{ operator }}\)
                        </div>
                      </div>
                    </div>

                    <div style="position:relative;"
                         class="ml-2"
                    >
                      <div class="py-1 px-2 text-center"
                           style="background-color: #ffffff; position:relative; cursor: pointer; user-select: none;
                           margin: 1px; border: rgb(133, 133, 133) solid 1px; border-radius: 4px"
                           @click="showSymbols = !showSymbols"
                           :style="`${showSymbols ? 'border: black solid 2px; margin: 0;' : ''}`"
                      >
                        \(\neq\)
                      </div>
                      <div v-show="showSymbols"
                           class=""
                           style="z-index: 100; background-color: #ffffff; position: absolute; top: 100%; left: 0;
                           margin-top: 8px; border: black 2px solid; border-radius: 4px; padding: 4px;
                           display: grid; grid-template-columns: repeat(7, 1fr); flex-wrap: wrap; text-align: center"
                      >
                        <div v-for="(symbol, index) in symbols"
                             :key="index"
                             @click="insertSymbol(symbol)"
                             class="letter"
                        >
                          \({{ symbol }}\)
                        </div>
                      </div>
                    </div>

                    <!--                    <div style="position:relative"-->
                    <!--                    >-->
                    <!--                      <div class="py-1 px-2 mt-2 text-center"-->
                    <!--                           style="background-color: #bebebe; position:relative; cursor: pointer; user-select: none"-->
                    <!--                           @click="showMathSymbols = !showMathSymbols"-->
                    <!--                      >-->
                    <!--                        мат. символы-->
                    <!--                      </div>-->
                    <!--                      <div v-show="showMathSymbols"-->
                    <!--                           class="py-1 px-1"-->
                    <!--                           style="z-index: 100; background-color: aquamarine; position: absolute; top: 0; left: 100%; margin-left: 8px; display: grid; grid-template-columns: repeat(8, 1fr); flex-wrap: wrap; text-align: center"-->
                    <!--                      >-->
                    <!--                        <div v-for="(letter, index) in letters"-->
                    <!--                             :key="index"-->
                    <!--                             style="margin: 0 2px"-->
                    <!--                             @click="insertLetter(letter)"-->
                    <!--                        >-->
                    <!--                          \({{ letter }}\)-->
                    <!--                        </div>-->
                    <!--                      </div>-->
                    <!--                    </div>-->
                  </div>
                </div>

                <textarea v-model="formula"
                          style="width: 100%"
                          rows="3"
                          @click="updateFormulaCursorPos"
                          @keydown="updateFormulaCursorPos"
                ></textarea>

                <div style="font-weight: bolder; margin: .8rem 0 .4rem">Результат:</div>
                <div ref="formulaPreview" style="margin: .5rem 0; height: 40px"></div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="showModal = false">Отмена</button>
                <button type="button" class="btn btn-primary"
                        v-if="isInEditMode"
                        @click="saveFormula"
                >Сохранить
                </button>
                <button type="button" class="btn btn-primary"
                        v-else
                        @click="addFormula"
                >Добавить в конец
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
