</template>

<script>


export default {
  name: 'wysiwygMath',
  props: {
    id: String,
    placeholder: String,
    rows: {
      type: Number,
      default: 5
    },
  },
  data: function () {
    return {
      showPopup: false,
      hideTimeout: null,
      preventHideFlag: false,
      // formula: '',
      formula: 'x = {-b \\pm \\sqrt{b^2-4ac} \\over 2a}',

      data: '',
      cursorPos: 0,
      formulaCursorPos: 0,

      fontStyles: [
        {tag: 'b', icon: 'fas fa-bold', style: 'bold', selected: false},
        {tag: 'i', icon: 'fas fa-italic', style: 'italic', selected: false},
        {tag: 'u', icon: 'fas fa-underline', style: 'underline', selected: false},
        {tag: 'strike', icon: 'fas fa-strikethrough', style: 'strikethrough', selected: false},
      ],

      fontSizes: [
        {name: 'x-small', value: '1'},
        {name: 'small', value: '2'},
        {name: 'medium', value: '3'},
        {name: 'large', value: '4'},
        {name: 'x-large', value: '5'},
        {name: 'xx-large', value: '6'},
        {name: 'xxx-large', value: '7'},
      ],

      showModal: false,
      isInEditMode: false,

      showLetters: false,
      showOperators: false,
      showSymbols: false,

      MQ: null,

      letters: [
        '\\alpha',
        '\\beta',
        '\\gamma',
        '\\delta',
        '\\epsilon',
        '\\zeta',
        '\\eta',
        '\\theta',
        '\\iota',
        '\\kappa',
        '\\lambda',
        '\\mu',
        '\\nu',
        '\\pi',
        '\\rho',
        '\\sigma',
        '\\tau',
        '\\upsilon',
        '\\phi',
        '\\chi',
        '\\psi',
        '\\omega',
      ],

      operators: [
        '\\sqrt{ab}',
        '\\sqrt[n]{ab}',
        '\\log_{a}{b}',
        '\\lg{ab}',
        'a^{b}',
        'a_{b} c_a^b',
        'c_a^b',
        '\\widetilde{ab}',
        '\\widehat{ab}',
        '\\overrightarrow{ab}',
        '\\overline{ab}',
        '\\frac{ab}{cd}',
        '\\frac{\\partial a}{\\partial b}',
        '\\frac{\\text{d}x}{\\text{d}y}',
        '\\lim_{a \\rightarrow b}',
        '\\int_{a}^{b}',
        '\\prod_a^b',
        '\\bigcap_a^b',
        '\\bigcup_a^b',
        '\\sum_a^b',
        '\\left(\\begin{array}{c}a\\\\ b\\end{array}\\right)',
        '\\begin{bmatrix}a & b \\\\c & d \\end{bmatrix}',
        '\\begin{cases}a & x = 0\\\\b & x > 0\\end{cases}',
      ],

      symbols: [
        '\\times',
        '\\div',
        '\\pm',
        '\\mp',
        '\\cdot',
        '\\star',
        '\\neq',
        '\\leq',
        '\\geq',
        '\\sim',
        '\\approx',
        '\\cong',
        '\\equiv',
        '\\ll',
        '\\gg',
        '\\in',
        '\\subset',
        '\\subseteq',
        '\\ni',
        '\\models',
        '\\vdash',
        '\\perp',
        '\\mid',
        '\\parallel',
        '\\hat{a}',
        '\\check{a}',
        '\\acute{a}',
        '\\tilde{a}',
        '\\bar{a}',
        '\\vec{a}',
        '\\dot{a}',
        '\\ddot{a}',
        '\\leftarrow',
        '\\Leftarrow',
        '\\rightarrow',
        '\\Rightarrow',
        '\\leftrightarrow',
        '\\Leftrightarrow',
        '\\exists',
        '\\forall',
        '\\triangle',
        '\\triangledown',
      ],
    }
  },
  watch: {
    cursorPos() {
      this.analyze();
    },
    formula() {
      this.$refs.formulaPreview.style.opacity = 0;
      this.$refs.formulaPreview.innerHTML = `\\(${this.formula}\\)`;
      setTimeout(() => {
        window.MathJax.typeset();
        this.$refs.formulaPreview.style.opacity = 1;
      }, 200);
    },

    showLetters() {
      if (this.showLetters) {
        this.showOperators = false;
        this.showSymbols = false;
      }
    },
    showOperators() {
      if (this.showOperators) {
        this.showLetters = false;
        this.showSymbols = false;
      }
    },
    showSymbols() {
      if (this.showSymbols) {
        this.showLetters = false;
        this.showOperators = false;
      }
    },
  },
  methods: {
    preventHide() {
      // console.log('preventHide')
      this.preventHideFlag = true;
      //
      // if (this.hideTimeout)
      //   clearInterval(this.hideTimeout);
    },

    setStyle(style, showUI = false, value = null, pressButton = false) {
      // this.preventHide();
      document.execCommand(style, showUI, value);

      if (pressButton) {
        let fontStyle = this.fontStyles.find(fontStyle => fontStyle.style === style)
        if (fontStyle) {
          fontStyle.selected = !fontStyle.selected;
        }
      }

      // editor.trigger('keyup')
    },
    hideSelf() {
      // console.log('hideSelf')

      if (!this.preventHideFlag) {
        this.showPopup = false;
      } else {
        this.preventHideFlag = !this.preventHideFlag;
      }
      // this.hideTimeout = setTimeout(() => {
      //   this.showPopup = false;
      // }, 200)
    },
    createFormula() {
      this.isInEditMode = false;
      this.showModal = true;
      // this.renderFormula();
      setTimeout(() => {
        window.MathJax.typeset();
        this.$refs.formulaPreview.style.opacity = 1;
      }, 200);
    },

    onDivInput(event) {
      this.$emit('input', event.target.innerHTML)
      this.data = event.target.innerHTML;
    },
    onDataRawInput() {
      this.$refs.textarea.innerHTML = this.data;
    },

    // editFormula() {
    //   this.isInEditMode = true;
    //   this.showModal = true;
    // },

    // handleInput(e) {
    //   this.divData = e.target.innerHTML;
    // },

    getCursorPos() {
      let _range = document.getSelection().getRangeAt(0)
      let range = _range.cloneRange()
      range.selectNodeContents(this.$refs.textarea)
      range.setEnd(_range.endContainer, _range.endOffset)
      return range.toString().length;
    },
    updateCursorPos() {
      this.cursorPos = this.getCursorPos();
      // setTimeout(() => {
      //   this.cursorPos = this.getCursorPos();
      // }, 100);
    },

    updateFormulaCursorPos(event) {
      this.formulaCursorPos = event.target.selectionStart;
    },

    insertToFormula(symbol) {
      this.formula = this.formula.slice(0, this.formulaCursorPos) + ` ${symbol} ` + this.formula.slice(this.formulaCursorPos);
    },
    insertLetter(symbol) {
      this.showLetters = false;
      this.insertToFormula(symbol);
    },
    insertOperator(symbol) {
      this.showOperators = false;
      this.insertToFormula(symbol);
    },
    insertSymbol(symbol) {
      this.showSymbols = false;
      this.insertToFormula(symbol);
    },

    analyze() {
      let selection = window.getSelection(), parentEl, tags = [];
      if (selection.rangeCount) {
        parentEl = selection.getRangeAt(0).commonAncestorContainer;

        // eslint-disable-next-line no-constant-condition
        while (true) {
          parentEl = parentEl.parentNode;

          if (parentEl == null)
            break;

          let parentName = parentEl.nodeName.toLowerCase();
          if (parentEl === this.$refs.textarea) {
            break;
          }
          tags.push(parentName);
        }
      }
      this.fontStyles.forEach(fontStyle => fontStyle.selected = tags.includes(fontStyle.tag));
    },

    // preventEnter(event) {
    //   if (event.keyCode === 13) {
    //     console.log('insertHTML')
    //     document.execCommand('insertHTML', false, '<br>');
    //     event.preventDefault();
    //     return false
    //   }
    // },

    increaseFont() {
      // this.preventHide();

      let parent = window.getSelection().getRangeAt(0).commonAncestorContainer.parentElement
      if (parent !== this.$refs.textarea) {
        if (parent.style.fontSize !== '') {
          let index = this.fontSizes.map(obj => obj.name).indexOf(parent.style.fontSize);
          if (index < this.fontSizes.length - 1) {
            document.execCommand('styleWithCSS', false, true);
            document.execCommand('fontSize', false, this.fontSizes[index + 1].value);
            document.execCommand('styleWithCSS', false, false);
          }
        }
      } else {
        document.execCommand('styleWithCSS', false, true);
        document.execCommand('fontSize', false, this.fontSizes[3].value);
        document.execCommand('styleWithCSS', false, false);
      }
    },

    decreaseFont() {
      // this.preventHide();

      let parent = window.getSelection().getRangeAt(0).commonAncestorContainer.parentElement
      if (parent !== this.$refs.textarea) {
        if (parent.style.fontSize !== '') {
          let index = this.fontSizes.map(obj => obj.name).indexOf(parent.style.fontSize);
          if (index > 0) {
            document.execCommand('styleWithCSS', false, true);
            document.execCommand('fontSize', false, this.fontSizes[index - 1].value);
            document.execCommand('styleWithCSS', false, false);
          }
        }
      } else {
        document.execCommand('styleWithCSS', false, true);
        document.execCommand('fontSize', false, this.fontSizes[1].value);
        document.execCommand('styleWithCSS', false, false);
      }
    },

    addFormula() {
      // let cursorPos = this.getCursorPos();

      // let formula = '\\(x = {-b \\pm \\sqrt{b^2-4ac} \\over 2a}\\)';

      // let aaa = this.$refs.textarea.innerHTML;

      // this.divData = aaa.slice(0, cursorPos) + formula + aaa.slice(cursorPos);
      // this.divData = aaa.slice(0, cursorPos) + `<span contenteditable="false" @click="editFormula">${formula}</span>` + aaa.slice(cursorPos);

      // this.$refs.textarea.innerHTML = this.$refs.textarea.innerHTML + formula + '<br>';
      // this.divData = this.$refs.textarea.innerHTML + `<span contenteditable="false" @click="editFormula">${formula}</span>`;

      // this.$refs.textarea.innerHTML = this.$refs.textarea.innerHTML + `<span contenteditable="false" @click="editFormula">${formula}</span>`;

      let data = `<span contenteditable="false" class="formula" data-raw="${this.formula}" ` +
          '> ' + this.$refs.formulaPreview.innerHTML + ' </span><br>';

      this.$refs.textarea.innerHTML += data;

      this.data += data;

      setTimeout(window.MathJax.typeset, 200);

      this.showModal = false;
      this.formula = '';
    },

    editFormula(event) {
      console.log('editFormula')
      console.log(event.target)
    },

    saveFormula() {
    },
    deleteFormula() {
    },
  },
  mounted() {
    console.log(window.MathQuill)
    this.MQ = window.MathQuill.getInterface(2);
  }
}
</script>

